import React from "react";
import PropTypes from "prop-types";
import { StaticQuery, graphql } from "gatsby";
import { withPreview } from "@prismicio/gatsby-source-prismic-graphql";
import { Helmet } from "react-helmet";

const query = graphql`
  query SEOMetaData {
    site {
      siteMetadata {
        siteUrl
        title
        description
        keywords
      }
    }
  }
`;

const SEO = ({ title, description, keywords, lang, meta }) => (
  <StaticQuery
    query={`${query}`}
    render={withPreview(({ site }) => {
      const siteTitle = site.siteMetadata?.title;
      const siteDescription = site.siteMetadata?.description;
      const siteKeywords = site.siteMetadata?.keywords;
      const siteAuthor = site.siteMetadata?.author;

      const titleTemplate = title === siteTitle ? "%s" : `%s | ${siteTitle}`;
      const metaDescription = description || siteDescription;
      const metaKeywords = keywords || siteKeywords;

      return (
        <Helmet
          htmlAttributes={{
            lang,
          }}
          title={title}
          titleTemplate={titleTemplate}
          meta={[
            {
              name: `description`,
              content: metaDescription,
            },
            {
              name: `keywords`,
              content: metaKeywords,
            },
            {
              property: `og:title`,
              content: title,
            },
            {
              property: `og:description`,
              content: metaDescription,
            },
            {
              property: `og:type`,
              content: `website`,
            },
            {
              name: `twitter:card`,
              content: `summary`,
            },
            {
              name: `twitter:creator`,
              content: siteAuthor,
            },
            {
              name: `twitter:title`,
              content: title,
            },
            {
              name: `twitter:description`,
              content: metaDescription,
            },
          ].concat(meta)}
        />
      );
    }, query)}
  />
);

SEO.defaultProps = {
  lang: "en",
  meta: [],
  description: "",
  keywords: "",
};

SEO.propTypes = {
  description: PropTypes.string,
  keywords: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
};

export default SEO;
